.homeRaid{
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-size: cover;
    background-repeat: no-repeat;
    background: url("https://images.mein-mmo.de/medien/2018/03/Destiny-Wallpaper.jpg");
}
.homeRaid p{
    display: flex;
    align-items: center;
    justify-content: center;
    background: #00000040;
    box-shadow: 0 8px 32px 0 #ffffff5e;
    backdrop-filter: blur( 4px );
    -webkit-backdrop-filter: blur( 4px );
    border-radius: 10px;
    border: 1px solid #ffffff2e;
    padding: 10px 35px;
    width: 90%;
}
.footerRaid nav{
    position: relative;
    width: 100%;
    top: calc(79px + 1vh);
    height: 70px;
    background-color: aliceblue;
    display: flex;
    justify-content: center;
    align-items: center;
}
.footerRaid nav ul{
    display: inline-flex;
}
.footerRaid nav ul li{
    cursor: pointer;
    margin: 10px;
    color: var(--background);
}
.footerRaid nav ul li:hover{
    background-color: aqua
}